    if (userInfo === '' || userInfo === null) return 'rgba(0, 0, 0, 0.2)';
    const status = userInfo.likeComment.some((item) => item === timeStamp);
    if (status) {
      return 'rgb(234, 92, 92)';
    } else {
      return 'rgba(0, 0, 0, 0.2)';
    }
  };
});

